.jobapply-container {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 80%;
  height: 100%;
  padding: 0px 50px;
  justify-content: start;
  align-self: start;
  overflow-y: scroll;
  position: relative;
}

.exit-jobapply {
  background-color: red;
  color: white;
  padding: 5px;
  border: 2px solid black;
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.exit-jobapply:hover {
  background-color: white;
  color: red;
  border: 2px solid red;
}
